import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "orderBy"
})
export class OrderByPipe implements PipeTransform {
  transform(array: Array<any>, args: any) : Array<any> {
    console.log(array, args)

    setTimeout(function() {
        array.sort((a: any, b: any) => {
            console.log(a[args])

            if (a[args] < b[args]) {
                return -1;
            }

            if (a[args] > b[args]) {
                return 1;
            }

            return 0;
        });
    }, 1000);
    
    return array;
  }
}


